<template>
  <div class="DataView-style">
    <div v-if="cur_source_id">
      <div class="title">
      <h1>{{' #'+ cur_source_id}}</h1>
        <span>{{update_time}}</span>
      </div>
      <el-tabs v-model="cur_page">
        <el-tab-pane label="" name="DotsData"></el-tab-pane>
        <el-tab-pane label="" name="SlopeData"></el-tab-pane>
      </el-tabs>
      <component :is="cur_page"></component> <!--  el-tab-pane  resize -->
    </div>
    <div v-else class="no_content">
      <div>
        <i class="el-icon-warning warning_icon"></i>
        
        <br>
        
      </div>
    </div>
  </div>
</template>

<script>
  import DotsData from './DotsData.vue';
  import SlopeData from './SlopeData.vue';

  export default {
    data() {
      return {
        cur_page: 'DotsData'
      };
    },
    computed: {
      cur_source_id() {
        return this.$store.state.cur_source_id;
      },
      latest_data() {
        return this.$store.state.database.get(this.cur_source_id).getLatest();
      },
      update_time() {
        return new Date(this.latest_data.time).toLocaleString();
      }
    },
    components: {
      DotsData,
      SlopeData
    }
  };
</script>

